<template>
  <div>
    <van-tabs sticky v-model="active"
      color="#e02e24"
      title-inactive-color="#444"
      title-active-color="#e02e24"
    >
      <van-tab title="热门">
        <hot />
      </van-tab>
      <template v-if="resouce">
        <van-tab :title="e.name" v-for="(e, i) in resouce.category" :key="i">
          <normal :data="resouce.list[i]"/>
        </van-tab>
      </template>
    </van-tabs>
    <mtab />
  </div>
</template>

<script>
import Hot from './Hot'
import Mtab from '#/Common/Tab'
import Normal from './Normal'
import { mapState } from 'vuex'
export default {
  name: 'Home',
  components: {
    Hot,
    Mtab,
    Normal,
  },
  computed: {
    ...mapState('goods', ['resouce'])
  },
  data() {
    return {
      active: 0,
    }
  },
  created() {
    this.$store.dispatch('goods/category')
  },
}
</script>
<style scoped lang="scss">

</style>



