<template>
  <div class="good-list">
    <div v-for="(e, i) in data" :key="i" class="good-list-item">
      <div class="img-wrap" :style="{width: '3.1rem',height: '3.1rem'}">
        <van-image
          width="3.1rem"
          height="3.1rem"
          fit="contain"
          :src="e.url"
        />
      </div>
      <div class="cont" :style="{marginLeft: '3.3rem'}">
        <p class="title">{{e.title}}</p>
        <div class="bar">
          <p class="bar-price"><span>￥</span>{{e.price}}</p>
          <p class="bar-count">{{e.count | desc}}</p>
        </div>      
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: 'GoodList',
  props: {
    data: {
      type: Array,
    }
  },
  data () {
    return {
    }
  },
  created() {

  },
  filters: {
    desc(val) {
      return `已拼${val}件`
    }
  },
  methods: {
  },
}
</script>
<style scoped lang="scss">
.good-list-item {
  position: relative;
  background-color: #fff;
  padding: .1rem;
}
.img-wrap {
  position: absolute;
  left: .2rem;
  top: .2rem;
  overflow: hidden;
  border-radius: 3px;
}
.cont {
  position: relative;
  display: flex;
  height: 3.1rem;
  padding: .1rem;
  flex-flow: column;
  justify-content: space-between;
  .title {
    font-size: 14px;
  }
  .bar {
    display: flex;
    align-items: flex-end;
    &-price {
      color: #f7332a;
      font-size: 18px;
      span {
        font-size: 14px;
      }
    }
    &-count {
      margin-left: .2rem;
      color: #a0a0a0;
      font-size: 14px;
    }
  }
}
</style>



