<template>
  <div v-if="data" class="normal">
    <van-grid :column-num="5">
      <van-grid-item
        v-for="(e, i) in data.keywords"
        :key="i"
        :icon="e.imgurl"
        :text="e.name"
      />
    </van-grid>
    <div class="ad">
      <img :src="data.ad" alt="">
    </div>
    <div class="goods">
      <router-link tag="div" :key="i" class="goods-item"
        :to="{path:'goods/'+e.id}"
        v-for="(e, i) in data.goods"
        @click.native="current(e)"
      >
        <van-image width="50vw" height="50vw" :src="e.picurl[0]" />
        <div class="info">
          <div class="title">{{e.name}}</div>
          <div class="tags">
            <span v-for="(e, i) in e.tag" :key="i">{{e}}</span>
          </div>
          <div class="price">
            <span>￥</span><span>{{e.price}}</span><span>已拼{{e.count}}件</span>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import { Toast } from 'vant'
export default {
  name: 'Normal',
  props: ['data'],
  methods: {
    ...mapMutations('goods', ['current'])
  },
  mounted() {
    Toast('数据全部是基于mockjs')
  }
}
</script>
<style scoped lang="scss">
@import '@/assets/css/util.scss';
.ad {
  height: 1.6rem;
  margin: .2rem 0;
  background-color: #eee;
  img {
    @include img(100vw, 1.6rem);
  }
}
.goods {
  overflow: hidden;
  @include fm-c();
  &-item {
    width: 50vw;
    .info {
      width: 100%;
      padding: 0 .2rem;
      .title {
        @include cb(#151516, null, .26rem);
        @include te();
        height: 0.5rem;
      }
      .tags {
        @include cb(#e2c835, null, .24rem);
        height: 0.3rem;
        span {
          @include tag(.24rem, #fff2eb, 0 .1rem, #ff8c54);
        }
      }
      .price {
        height: 0.7rem;
        span:nth-child(1) {
          @include cb(#e23e35, null, .24rem);
        }
        span:nth-child(2) {
          @include cb(#e23e35, null, .34rem);
          font-weight: 700;
        }
        span:nth-child(3) {
          @include cb(#9c9c9c, null, .24rem);
          padding-left: .2rem;
        }
      }
    }    
  }
}

</style>



