<template>
  <div>
    <!-- swiper -->
    <van-swipe :loop="false" 
      v-bind="bind">
      <van-swipe-item v-for="(e, i) in page" :key="i">
        
        <!-- grid layout -->
        <van-grid :border="border" :column-num="columns" :square="true">
          <van-grid-item v-for="(e, i) in getCell(i)" :key="i">
            <van-image slot="icon" :src="e.url" :width="iconWidth"/>
            <p slot="text" :style="{fontSize: '.12rem'}">{{e.title}}</p>
          </van-grid-item>
        </van-grid>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>

export default {
  name: 'ScrollGrid',
  props: {
    autoplay: {
      type: Number,
      default: 0,
    },
    height: {
      type: Number,
    },
    width: {
      type: Number,
    },
    'icon-width': {
      type: String,
      default: 'auto',
    },
    'icon-height': {
      type: String,
      default: 'auto',
    },
    data: {
      type: Array,
      required: true,
    },
    'columns': {
      type: Number,
      default: 4,
    },
    'rows': {
      type: Number,
      default: 2,
    },
    border: {
      type: Boolean,
      default: true,
    }
  },
  data () {
    return {
    }
  },
  created() {

  },
  computed: {
    page() {
      return Math.ceil(this.data.length / this.count)
    },
    count() {
      return this.rows * this.columns
    },
    bind() {
      let obj = {}
      if (this.width != null) obj['width'] = this.width 
      if (this.height != null) obj['height'] = this.height 
      if (this.autoplay != null) obj['autoplay'] = this.autoplay
      return obj
    }
  },
  methods: {
    getCell(i) {
      return this.data.slice(i * this.count, Math.min((i+1) * this.count, this.data.length))
    }
  },
}
</script>
<style scoped lang="scss">
</style>



