<template>
  <div>
    <!-- swiper layout -->
    <van-swipe :autoplay="3000">
      <van-swipe-item v-for="(image, index) in swipeImgs" :key="index">
        <img v-lazy="image" :style="{height: '3rem'}"/>
      </van-swipe-item>
    </van-swipe>

    <!-- main grid layout -->
    <scroll-grid :data="gridData" :columns="5" icon-width="0.8rem" :border=false />

    <!-- sub grid layout -->
    <div class="sub-grid">
      <div class="sub-grid-title">
        <h3>{{subGridData.title}}</h3>
        <p>{{subGridData.subtitle}}</p>
      </div>
      <ul class="sub-grid-list">
        <li v-for="(e, i) in subGridData.list" :key="i">
          <img :src="e.url" :style="{width:108}" alt="">
          <span>{{e.tag | discount}}</span>
        </li>
      </ul>
    </div>

    <!-- main ad -->
    <div class="ad">
      <van-image
        height="2.22rem"
        fit="contain"
        :src="adData"
      />
    </div>

    <!-- goodlist -->
    <good-list :data="goodListData" />

    <shop-msg :data="shopData"/>
    <good-list :data="goodListData" />
    <shop-msg :data="shopData"/>
  </div>
</template>

<script>
import ScrollGrid from '#/Common/ScrollGrid'
import GoodList from '#/Common/GoodList'
import ShopMsg from '#/Common/ShopMsg'
import { Toast } from 'vant'
export default {
  name: 'Hot',
  components: {
    ScrollGrid,
    GoodList,
    ShopMsg,
  },
  data() {
    return {
      swipeImgs: [
        '/static/imgs/b01.jpg',
        '/static/imgs/b02.jpg',
        '/static/imgs/b03.jpg',
        '/static/imgs/b04.jpg',
      ],
      gridData: [
        { title: '限时秒杀', url: './static/imgs/g01.gif' },
        { title: '断码清仓', url: './static/imgs/g02.png' },
        { title: '品牌馆', url: './static/imgs/g03.png' },
        { title: '多多果园', url: './static/imgs/g04.png' },
        { title: '9块9特卖', url: './static/imgs/g05.png' },
        { title: '助力享免单', url: './static/imgs/g06.png' },
        { title: '天天领现金', url: './static/imgs/g07.png' },
        { title: '1分抽大奖', url: './static/imgs/g08.png' },
        { title: '省钱月卡', url: './static/imgs/g09.png' },
        { title: '充值中心', url: './static/imgs/g10.png' },
        { title: '百亿补贴', url: './static/imgs/g11.png' },
        { title: '现金签到', url: './static/imgs/g12.png' },
        { title: '金猪赚大钱', url: './static/imgs/g13.png' },
        { title: '电器城', url: './static/imgs/g14.jpeg' },
        { title: '爱逛街', url: './static/imgs/g15.png' },
        { title: '砍价免费拿', url: './static/imgs/g16.png' },
        { title: '多多爱消除', url: './static/imgs/g17.png' },
        { title: '每日好店', url: './static/imgs/g18.png' },
        { title: '每日好店', url: './static/imgs/g18.png' },
        { title: '每日好店', url: './static/imgs/g18.png' },
      ],
      subGridData: {
        title: '品牌特卖',
        subtitle: '买华美送手机',
        list: [
          { url: './static/imgs/a01.webp', tag: '5.1' },
          { url: './static/imgs/a02.webp', tag: '0.8' },
          { url: './static/imgs/a03.webp', tag: '1.4' },
          { url: './static/imgs/a04.webp', tag: '4.9' },
        ]
      },
      adData: '/static/imgs/a05.webp',
      goodListData: [
        { url: './static/imgs/good-01.webp', title: '【正品国行】Dyson戴森 吹风机 Supersonic HD01 紫红色', price: 2480, count: 1499 },
        { url: './static/imgs/good-01.webp', title: '【正品国行】Dyson戴森 吹风机 Supersonic HD01 紫红色', price: 2480, count: 1499 },
        { url: './static/imgs/good-01.webp', title: '【正品国行】Dyson戴森 吹风机 Supersonic HD01 紫红色', price: 2480, count: 1499 },
        { url: './static/imgs/good-01.webp', title: '【正品国行】Dyson戴森 吹风机 Supersonic HD01 紫红色', price: 2480, count: 1499 },
        { url: './static/imgs/good-01.webp', title: '【正品国行】Dyson戴森 吹风机 Supersonic HD01 紫红色', price: 2480, count: 1499 },
        { url: './static/imgs/good-01.webp', title: '【正品国行】Dyson戴森 吹风机 Supersonic HD01 紫红色', price: 2480, count: 1499 },
      ],
      shopData: {
        tag: '全店5折',
        name: '七匹狼',
        url: '/static/imgs/s00.webp',
        goods: [
          { url: './static/imgs/s01.webp', price: 98, discount: 49 },
          { url: './static/imgs/s02.webp', price: 98, discount: 49 },
          { url: './static/imgs/s03.webp', price: 98, discount: 49 },
          { url: './static/imgs/s04.webp', price: 98, discount: 49 },
          { url: './static/imgs/s05.webp', price: 98, discount: 49 },
        ]
      }
    }
  },
  filters: {
    discount(val) {
      return val + '折'
    },
  },
  mounted() {
    Toast({
      message:'热门布局展示，其它为功能演示',
      duration: 5000,
    })
  },
}
</script>
<style scoped lang="scss">
.sub-grid {
  display: flex;
  height: 1.5rem;
  margin: .16rem 0;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  &-title {
    margin-left: .2rem;
    h3 {
      margin: .1rem 0;
      font-size: 16px;
    }
    p {
      position: relative;
      font-size: 13px;
    }
  }
  &-list {
    display: flex;
    align-items: center;
    justify-content: space-around;
    li {
      position: relative;
      border-radius: 3px;
      border: 1px solid #f6f6f6;
      margin-right: .1rem;
      img {
        display: block;
        width: 1.08rem;
      }
      span {
        position: absolute;
        left: 0;
        bottom: 0;
        padding: 0 .1rem;
        font-size: 12px;
        border-radius: 3px;
        color: #87562b;
        background-color: #ffd198;
      }
    }
  }
}

</style>



