<template>
  <div class="shop-msg">
    <div class="title">
      <div class="img-wrap">
        <img :src="data.url" width="84" alt="">
      </div>
      <div class="info">
        <h3>{{data.name}}</h3>
        <p class="tag">{{data.tag}}</p>
      </div>
      <p class="entry">进店逛逛&gt;</p>
    </div>
    <div class="list">
      <ul>
        <li v-for="(e, i) in data.goods" :key="i">
          <div class="img-wrap">
            <img :src="e.url" width="228" alt="">
          </div>
          <div class="msg">
            <span class="prefix">￥</span><strong class="discount">{{e.discount}}</strong><s class="price">￥{{e.price}}</s>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: 'GoodList',
  props: {
    data: {
      type: Object,
    }
  },
}
</script>
<style scoped lang="scss">
.shop-msg {
  position: relative;
  margin: .2rem 0;
  padding: .2rem .2rem .28rem .22rem;
  background-color: #fff;
  .title {
    position: relative;
    height: 0.84rem;
    margin-right: .2rem;
    margin-bottom: .2rem;
    padding-left: 1.04rem;
    .img-wrap {
      position: absolute;
      top: 0;
      left: 0;
      width: .84rem;
      img {
        width: .84rem;
      }
    }
    .info {
      height: 0.84rem;
      h3 {
        font-size: 18px;
      }
      .tag {
        color: #e02f25;
        font-size: 14px;
        border-radius: 3px;
      }    
    }
    .entry {
      position: absolute;
      right: .2rem;
      top: 50%;
      transform: translateY(-50%);
      color: #a7a1a0;
      font-size: 16px;
    }
  }
  .list {
    height: 2.95rem;
    ul {
      overflow-x: auto;
      white-space: nowrap;
      width: auto;
      li {
        display: inline-block;
        margin-right: .1rem;
        img {
          width: 2.28rem;
          height: 2.28rem;
        }
        .msg {
          color: #e13326;
          text-align: center;
          .prefix {
            font-size: 12px;
          }
          .discount {
            font-size: 16px;
          }
          .price {
            font-size: 14px;
            color: #aaa2a5;
          }
        }
      }
    }
  }
}
</style>



